            <li><kbd>Ctrl + Shift + L</kbd> / <kbd>Ctrl + F2</kbd> - Select all occurrences of current selection</li>
            <li><kbd>Ctrl + D</kbd> - Select next occurrence of current selection</li>
            <li><kbd>Alt + Click</kbd> - Insert cursor</li>
            <li><kbd>Ctrl + Alt + Up/Down</kbd> - Insert cursor above/below</li>
            <li><kbd>Shift + Alt + Drag</kbd> - Column (box) selection</li>
          </ul>
          <h3>Editing</h3>
          <ul>
