          <h3>Navigation</h3>
          <ul>
            <li><kbd>Ctrl + G</kbd> - Go to Line...</li>
            <li><kbd>Ctrl + P</kbd> - Quick Open, Go to File...</li>
            <li><kbd>Ctrl + Shift + O</kbd> - Go to Symbol...</li>
            <li><kbd>Ctrl + Shift + M</kbd> - Show Problems panel</li>
            <li><kbd>F8</kbd> - Go to next error or warning</li>
