<script>
import {
  PlusSmIcon, DotsVerticalIcon,
  DuplicateIcon,
  TrashIcon,
  QuestionMarkCircleIcon,
  ArrowCircleUpIcon,
  ArrowCircleDownIcon,
  ExclamationIcon,
  PencilIcon,
  CodeIcon,
  PuzzleIcon
} from '@heroicons/vue/solid'
import { VTooltip } from 'floating-vue'
import { Menu, MenuButton, MenuItem, MenuItems } from '@headlessui/vue'
import Divider from '@/components/Divider'
import { mapState } from 'pinia'
import { useMainStore } from '@/stores/main'
import DividerWithButtons from '@/components/DividerWithButtons'
import axios from '@/http'
import MonacoEditor from 'monaco-editor-vue3'
import { indicatorsInfo, devType, sourceType, matype, apiReference, apiFunctionReference, utilsReference } from '@/data/indicatorsInfo.js'
import * as monaco from 'monaco-editor'

export default {
  name: 'Routes',
  components: {
    Divider,
    PlusSmIcon,
    DotsVerticalIcon,
    Menu,
    MenuButton,
    MenuItem,
    MenuItems,
    DuplicateIcon,
    TrashIcon,
    ArrowCircleUpIcon,
    ArrowCircleDownIcon,
    DividerWithButtons,
    ExclamationIcon,
    PencilIcon,
    MonacoEditor,
    CodeIcon,
    PuzzleIcon,
    QuestionMarkCircleIcon
  },
  directives: {
    tooltip: VTooltip,
  },
  props: {
    strategyName: {
      type: String,
      default: ''
    },
    form: {
      type: Object,
      required: true
    },
    results: {
      type: Object,
      required: true
    }
  },
  emits: ['update-strategy-name'],
  data () {
    return {
      showHelpModal: false,
      showHotkeysModal: false,
      editorInstance: null,
      originalCode: '',
      strategyError: null,
      visibleStrategyOptions: null,
      editHistory: [],
      currentEditIndex: -1,
      oldRoutesCopy: JSON.parse(JSON.stringify(this.form.routes)),
      copiedExtraRoutes: { extra_routes: this.form.extra_routes },
      copiedRoutes: { routes: this.form.routes },
      totalRoutesError: [],
      currentStrategyName: '',
      code: '',
      editedStrategy: '',
      showEditor: false,
      editorKey: 0,
      autocompleteProvider: null,
      hoverProvider: null,
      editorOptions: {
        // Monaco Editor specific options
        find: {
          seedSearchStringFromSelection: true,
          autoFindInSelection: true,
        },
        readOnly: false,
        lineNumbers: 'on',
        wordWrap: 'on',
        roundedSelection: true,
        cursorStyle: 'line',
        matchBrackets: 'always',
        autoClosingBrackets: 'languageDefined',
        autoClosingQuotes: 'languageDefined',
        showUnused: true,
        glyphMargin: true,
        automaticLayout: true,
        parameterHints: { enabled: true },
        dragAndDrop: true,
        suggestOnTriggerCharacters: true,
        scrollBeyondLastLine: true,
        quickSuggestions: true,
        quickSuggestionsDelay: 10,
        minimap: { enabled: true },
        folding: true,
      },
    }
  },
  computed: {
    ...mapState(useMainStore, ['routes', 'jesseSupportedTimeframes', 'exchangeInfo', 'settings', 'strategies', 'planInfo']),
    isLive () {
      return this.$route.name === 'Live'
    },
    editorTheme () {
      return localStorage.theme === 'dark' ? 'vs-dark' : 'vs'
    },
    themeClass () {
      return localStorage.getItem('theme') === 'dark' ? 'night-theme' : 'day-theme'
    },
    formattedStrategyTitle () {
      return this.editedStrategy
        .split(' ')
        .map(word => word.charAt(0).toUpperCase() + word.slice(1))
        .join(' ')
    },
    errorModalMargin () {
      const baseMargin = 5 // Base bottom margin in pixels
      const extraMarginPerError = 4 // Additional margin per error message
      const numberOfErrors = this.strategyError ? this.strategyError.length : 0
      return `${baseMargin + (numberOfErrors * extraMarginPerError)}px`
    },
    exchanges () {
      const arr = []

      for (const key in this.exchangeInfo) {
        if (this.isLive) {
          if (this.exchangeInfo[key].modes.live_trading) {
            arr.push(this.exchangeInfo[key])
          }
        } else {
          if (this.exchangeInfo[key].modes.backtesting) {
            arr.push(this.exchangeInfo[key])
          }
        }
      }

      // sort by item.name alphabetically, however,
      // if item.name starts with "Bitget", put it first
      arr.sort((a, b) => {
        if (a.name.startsWith('Bitget')) {
          return -1
        } else if (b.name.startsWith('Bitget')) {
          return 1
        } else {
          return a.name.localeCompare(b.name)
        }
      })

      return arr
    }
  },
  watch: {
    strategyError (newErrors) {
      if (this.strategyError !== null) {
        this.setMonacoMarkers(this.editorInstance, newErrors)
      }
    },
    editorTheme (newTheme, oldTheme) {
      if (newTheme !== oldTheme) {
      // Force the component to update to apply the new theme
        this.forceRerender()
      }
    },
    code (newCode, oldCode) {
      if (newCode !== oldCode) {
        this.editHistory = this.editHistory.slice(0, this.currentEditIndex + 1)
        this.editHistory.push(newCode)
        this.currentEditIndex++
      }
    },
    'form.routes': {
      handler (newRoutes) {
        // Check if the current route includes 'backtest'
        if (this.$route.path.includes('backtest')) {
          this.handleFormRoutesChange(newRoutes)
        }
      },
      deep: true
    },
    form () {
      this.initiate()
    },
    copiedRoutes: {
      handler () {
        this.checkRoutes()
      },
      deep: true
    },
    copiedExtraRoutes: {
      handler () {
        this.checkRoutes()
      },
      deep: true
    }
  },
  created () {
    this.initiate()
  },
  mounted () {
    // Check if the component is loaded in the context where you need to run the handler
    if (this.$route.path.includes('backtest')) {
      // Manually invoke the handler for 'form.routes'
      if (this.form.routes.length > 0) {
        const lastStrategy = this.form.routes[this.form.routes.length - 1].strategy
        this.sendStrategyToBackend(lastStrategy)
      }
    }
  },
  methods: {
    backendFormatCode () {
      axios.post('/code-formatting', {
        code: this.code
      }).then(response => {
        if (response.status === 200) {
          this.code = response.data
        } else {
          console.log(response.data)
        }
      }).catch(error => {
        console.error(error)
      })
    },
    showHelp () {
      this.showHelpModal = true
    },
    closeHelpModal () {
      this.showHelpModal = false
    },
    showHotkeys () {
      this.showHotkeysModal = true
    },
    closeHotkeysModal () {
      this.showHotkeysModal = false
    },
    setMonacoMarkers (editor, errorMessages) {
      const markers = this.parseErrors(errorMessages)
      monaco.editor.setModelMarkers(editor.getModel(), 'python', markers)
    },
    parseErrors (errorMessages) {
      const errors = Array.isArray(errorMessages) ? errorMessages : [errorMessages]
      return errors.map(error => {
        let startLineNumber = 1
        let startColumn = 1
        let endColumn = 2
        let message = error

        const lineColRegex = /<string>:(\d+):(\d+): (.*)/
        const lineColMatch = error.match(lineColRegex)
        const lineOnlyRegex = /line (\d+)/
        const lineOnlyMatch = error.match(lineOnlyRegex)

        if (lineColMatch) {
          startLineNumber = parseInt(lineColMatch[1])
          startColumn = parseInt(lineColMatch[2])
          message = lineColMatch[3]

          // Extract the quoted string from the message and adjust endColumn
          const quotedStringMatch = message.match(/'([^']+)'/)
          if (quotedStringMatch) {
            endColumn = startColumn + quotedStringMatch[1].length // Length of the quoted string
          } else {
            endColumn = startColumn + 1 // Default if no quoted string is found
          }
        } else if (lineOnlyMatch) {
          startLineNumber = parseInt(lineOnlyMatch[1])
          const lineContent = this.editorInstance.getModel().getLineContent(startLineNumber)
          endColumn = lineContent.length + 1
        }

        return {
          severity: monaco.MarkerSeverity.Error,
          startLineNumber: startLineNumber,
          startColumn: startColumn,
          endLineNumber: startLineNumber,
          endColumn: endColumn,
          message: message
        }
      })
    },

    setErrorMessages (error) {
      if (typeof error === 'string') {
      // Split the string into an array of lines if it contains newlines
        this.strategyError = error.includes('\n') ? error.split('\n') : [error]
      } else if (Array.isArray(error)) {
        this.strategyError = error
      } else {
        this.strategyError = ['Unknown error']
      }
    },
    editorWillMount (monaco) {

    },
    editorDidMount (editor) {
      this.setupHoverProvider(editor)
      this.setupAutocompleteProvider(editor)

      // Format document initially
      editor.getAction('editor.action.formatDocument').run()

      // Hotkey for formatting document
