      this.editorInstance = editor
    },
    removeWhitespaceLines (model, editor) {
      let startLine = null
      let endLine = null

      // Find the start and end lines of the hyperparameters function's return statement
      const lineCount = model.getLineCount()
      for (let i = 1; i <= lineCount; i++) {
        const lineContent = model.getLineContent(i)
        if (lineContent.includes('def hyperparameters(self):')) {
          startLine = i
        }
        if (startLine && lineContent.trim().endsWith(']')) {
          endLine = i
          break
        }
      }

      if (startLine === null || endLine === null) {
        return // The hyperparameters function was not found
      }

      const edits = []
      for (let lineNumber = startLine; lineNumber <= endLine; lineNumber++) {
        const lineContent = model.getLineContent(lineNumber)
        if (lineContent.trim() === '' && lineNumber !== startLine && lineNumber !== endLine) {
          // Prepare to remove lines that are within the return statement and are empty or contain only whitespace
          edits.push({
            range: new monaco.Range(lineNumber, 1, lineNumber + 1, 1),
            text: ''
          })
        }
      }

      if (edits.length > 0) {
        // Apply edits
        edits.reverse().forEach(edit => {
          editor.executeEdits('', [edit])
        })
      }
    },
    isChangeInHyperparameters (change, model) {
      let hyperparametersStartLine = null
      let hyperparametersEndLine = null

      // Find the start and end lines of the hyperparameters function
      const lineCount = model.getLineCount()
      for (let i = 1; i <= lineCount; i++) {
        const lineContent = model.getLineContent(i)
        if (lineContent.includes('def hyperparameters(self):')) {
          hyperparametersStartLine = i + 1 // Assuming the return statement is on the next line
          continue
        }
        if (hyperparametersStartLine && lineContent.includes(']')) {
          hyperparametersEndLine = i
          break
        }
      }

      if (!hyperparametersStartLine || !hyperparametersEndLine) {
        return false
      }

      return change.range.startLineNumber >= hyperparametersStartLine &&
         change.range.endLineNumber <= hyperparametersEndLine
    },
    setupAutocompleteProvider (editor) {
      this.autocompleteProvider = monaco.languages.registerCompletionItemProvider('python', {
        triggerCharacters: ['.', '_', 'a', 'b', 'c', 'd', 'e', 'f', 'g', 'h', 'i', 'j', 'k', 'l', 'm', 'n', 'o', 'p', 'q', 'r', 's', 't', 'u', 'v', 'w', 'x', 'y', 'z'],

        provideCompletionItems: (model, position) => {
          const textUntilPosition = model.getValueInRange({
            startLineNumber: position.lineNumber,
            startColumn: 1,
            endLineNumber: position.lineNumber,
            endColumn: position.column
          })

          // Check if the text until the position is 'self.'
          if (textUntilPosition.endsWith('self.')) {
            const suggestions = Object.keys(apiReference)
              .filter(key => apiReference[key].autocompleteText)
              .map(key => ({
                label: key,
                kind: monaco.languages.CompletionItemKind.Method, // or other appropriate kind
                insertText: apiReference[key].autocompleteText,
                range: {
                  startLineNumber: position.lineNumber,
                  startColumn: position.column, // Length of 'self.'
                  endLineNumber: position.lineNumber,
                  endColumn: position.column
                }
              }))

            return { suggestions }
          }
          // Check if the user is typing 'def '
          if (textUntilPosition.endsWith('def ')) {
            const apiFunctionSuggestions = Object.keys(apiFunctionReference)
              .filter(key => apiFunctionReference[key].autocompleteText) // Filter keys with autocompleteText attribute
              .map(key => ({
                label: key,
                kind: monaco.languages.CompletionItemKind.Function,
                insertText: apiFunctionReference[key].autocompleteText,
                insertTextRules: monaco.languages.CompletionItemInsertTextRule.InsertAsSnippet,
                range: {
                  startLineNumber: position.lineNumber,
                  startColumn: position.column, // Cursor position right after 'def '
                  endLineNumber: position.lineNumber,
                  endColumn: position.column
                }
              }))
            console.log(apiFunctionSuggestions.length)
            return {
              suggestions: apiFunctionSuggestions
            }
          }

          if (textUntilPosition.endsWith('utils.')) {
            const utilsSuggestions = Object.keys(utilsReference)
              .filter(key => utilsReference[key].autocompleteText) // Filter keys with autocompleteText attribute
              .map(key => ({
                label: key,
                kind: monaco.languages.CompletionItemKind.Function,
                insertText: utilsReference[key].autocompleteText,
                insertTextRules: monaco.languages.CompletionItemInsertTextRule.InsertAsSnippet,
                range: {
                  startLineNumber: position.lineNumber,
                  startColumn: position.column, // Adjust start column to replace 'utils.'
                  endLineNumber: position.lineNumber,
                  endColumn: position.column
                }
              }))

            return {
              suggestions: utilsSuggestions
            }
          }

          // Updated regex to include multiple underscores and alphanumeric characters
          const partialIndicatorMatch = /ta\.([a-zA-Z0-9_]+)$/.exec(textUntilPosition)
          if (partialIndicatorMatch) {
            const partialIndicator = partialIndicatorMatch[1]
            if (indicatorsInfo[partialIndicator]) {
              const indicator = indicatorsInfo[partialIndicator]
              let insertText = indicator.autocompleteText

              // Check the line above the current position
              if (position.lineNumber > 1) {
                const lineAboveText = model.getLineContent(position.lineNumber - 1)
                const indicatorPattern = /def _indicator\d+/

                // If the line above matches the pattern, modify the insertText
                if (indicatorPattern.test(lineAboveText)) {
                  // Replace "candles" with "precalc_candles"
                  insertText = insertText.replace(/\bcandles\b/, 'precalc_candles')
                  // Replace "sequential=False" or "sequential=True" with "sequential=sequential"
                  insertText = insertText.replace(/\bsequential=(?:False|True)\b/, 'sequential=sequential')
                }
              }

              const additionalTextEdits = []
              if (indicator.hyperparameters && indicator.hyperparameters.length > 0) {
                const hyperparametersText = indicator.hyperparameters.map(param => {
                  return `{'name': '${param.name}', 'type': ${param.type}, 'min': ${param.min}, 'max': ${param.max}, 'default': ${param.default}, 'step': ${param.step}}`
                }).join(',\n')

                let hyperparametersStartLine
                const lineCount = model.getLineCount()
                for (let i = 1; i <= lineCount; i++) {
                  const lineContent = model.getLineContent(i)
                  if (lineContent.includes('def hyperparameters(self):')) {
                    hyperparametersStartLine = i + 1 // Assuming the return statement is on the next line
                    break
                  }
                }

                if (hyperparametersStartLine) {
                  let hyperparametersEndLine
                  for (let i = hyperparametersStartLine; i <= lineCount; i++) {
                    const lineContent = model.getLineContent(i)
                    if (lineContent.trim().endsWith(']')) {
                      hyperparametersEndLine = i
                      break
                    }
                  }

                  if (hyperparametersEndLine) {
                    const openingBracketIndex = model.getLineContent(hyperparametersStartLine).indexOf('[')
                    const insertionPoint = openingBracketIndex + 2 // After the opening bracket

                    // Prepare formatted hyperparameters text
                    let formattedHyperparametersText = hyperparametersText + ',\n'
                    const firstParameterLine = hyperparametersStartLine + 1

                    // Check if the first line after the opening bracket is empty or has whitespaces
                    if (model.getLineContent(firstParameterLine).trim() === '') {
                      // Remove the empty or whitespace line
                      additionalTextEdits.push({
                        range: {
                          startLineNumber: firstParameterLine,
                          startColumn: 1,
                          endLineNumber: firstParameterLine + 1,
                          endColumn: 1
                        },
                        text: ''
                      })
                      // Adjust the insertion point for the new hyperparameters
                      formattedHyperparametersText = hyperparametersText
                    }

                    // Insert new hyperparameters
                    additionalTextEdits.push({
                      range: {
                        startLineNumber: hyperparametersStartLine,
                        startColumn: insertionPoint,
                        endLineNumber: hyperparametersStartLine,
                        endColumn: insertionPoint
                      },
                      text: formattedHyperparametersText
                    })
                  }
                }
              }
              return {
                suggestions: [{
                  label: indicator.autocompleteText,
                  kind: monaco.languages.CompletionItemKind.Snippet,
                  insertText: insertText,
                  insertTextRules: monaco.languages.CompletionItemInsertTextRule.InsertAsSnippet,
                  range: {
                    startLineNumber: position.lineNumber,
                    startColumn: position.column - partialIndicator.length,
                    endLineNumber: position.lineNumber,
                    endColumn: position.column
                  },
                  additionalTextEdits: additionalTextEdits
                }]
              }
            }
          }

          // Check for 'ta.' to suggest all indicators
          if (/ta\.$/.test(textUntilPosition)) {
            return {
              suggestions: Object.keys(indicatorsInfo).map(key => ({
                label: key,
                kind: monaco.languages.CompletionItemKind.Function,
                insertText: key,
                range: {
                  startLineNumber: position.lineNumber,
                  startColumn: position.column,
                  endLineNumber: position.lineNumber,
                  endColumn: position.column
                }
              }))
            }
          }

          return { suggestions: [] }
        }
      })
    },
    setupHoverProvider (editor) {
      const formatAttributeName = (name) => {
        return name
          .replace(/([A-Z])/g, ' $1')
          .replace(/^./, (str) => str.toUpperCase())
          .trim()
      }
      this.hoverProvider = monaco.languages.registerHoverProvider('python', {
        provideHover: (model, position) => {
          const word = model.getWordAtPosition(position)
          if (!word) return
          // Handle indicator info hover
          if (indicatorsInfo[word.word]) {
            const indicator = indicatorsInfo[word.word]
            const hoverContents = []
            const attributesToShow = ['description', 'bullishCondition', 'bearishCondition', 'category', 'commonPairings', 'bestUsedFor', 'historicalReliability', 'customizationTips', 'expectedReturn', 'returnValues']

            attributesToShow.forEach(attr => {
              if (indicator[attr]) {
                if (typeof indicator[attr] === 'object' && !Array.isArray(indicator[attr])) {
                  for (const nestedAttr in indicator[attr]) {
                    if (Object.prototype.hasOwnProperty.call(indicator[attr], nestedAttr)) {
                      hoverContents.push({ value: `**${formatAttributeName(nestedAttr)}:** ${indicator[attr][nestedAttr]}` })
                    }
                  }
                } else {
                  // Handle simple attributes
                  hoverContents.push({ value: `**${formatAttributeName(attr)}:** ${indicator[attr]}` })
                }
              }
            })

            if (hoverContents.length > 0) {
              return {
                range: model.getWordUntilPosition(position).range,
                contents: hoverContents
              }
            }
          }
          const apiRefEntry = apiReference[word.word]
          if (apiRefEntry) {
            const hoverContents = []

            // Attributes to check in apiRefEntry
            const attributes = ['description', 'tip', 'tips', 'note', 'aliases', 'availableMetrics', 'warning', 'expectedReturn']

            // Loop through attributes and add them if available
            attributes.forEach(attr => {
              if (apiRefEntry[attr]) {
                const title = attr.charAt(0).toUpperCase() + attr.slice(1)
                hoverContents.push({ value: `**${title}:** ${apiRefEntry[attr]}` })
              }
            })

            // Add example if available, formatted as a code block
            if (apiRefEntry.example) {
              hoverContents.push({ value: `**Example:**\n\`\`\`python\n${apiRefEntry.example}\n\`\`\`` })
            }

            // Return the hover content if there's any
            if (hoverContents.length > 0) {
              return {
                range: model.getWordUntilPosition(position).range,
                contents: hoverContents
              }
            }
          }
          const apiFuncRefEntry = apiFunctionReference[word.word]
          if (apiFuncRefEntry) {
            const hoverContents = []

            // Attributes to check in apiFuncRefEntry
            const attributes = ['description', 'tip', 'tips', 'note', 'aliases', 'availableMetrics', 'warning', 'expectedReturn']

            // Loop through attributes and add them if available
            attributes.forEach(attr => {
              if (apiFuncRefEntry[attr]) {
                const title = attr.charAt(0).toUpperCase() + attr.slice(1)
                hoverContents.push({ value: `**${title}:** ${apiFuncRefEntry[attr]}` })
              }
            })

            // Add example if available, formatted as a code block
            if (apiFuncRefEntry.example) {
              hoverContents.push({ value: `**Example:**\n\`\`\`python\n${apiFuncRefEntry.example}\n\`\`\`` })
            }

            // Return the hover content if there's any
            if (hoverContents.length > 0) {
              return {
                range: model.getWordUntilPosition(position).range,
                contents: hoverContents
              }
            }
          }

          const utilsRefEntry = utilsReference[word.word]
          if (utilsRefEntry) {
            const hoverContents = []

            // Attributes to check in utilsRefEntry
            const attributes = ['description', 'tip', 'tips', 'warning', 'returnType']

            // Loop through attributes and add them if available
            attributes.forEach(attr => {
              if (utilsRefEntry[attr]) {
                const title = attr.charAt(0).toUpperCase() + attr.slice(1)
                hoverContents.push({ value: `**${title}:** ${utilsRefEntry[attr]}` })
              }
            })

            // Add example if available, formatted as a code block
            if (utilsRefEntry.example) {
              hoverContents.push({ value: `**Example:**\n\`\`\`python\n${utilsRefEntry.example}\n\`\`\`` })
            }

            // Return the hover content if there's any
            if (hoverContents.length > 0) {
              return {
                range: model.getWordUntilPosition(position).range,
                contents: hoverContents
              }
            }
          }


          if (word.word === 'precalc_candles' || word.word === 'self.precalc_candles') {
            const description = apiReference.precalc_candles.description
            const hoverContents = [{ value: `**precalc_candles:** ${description}` }]
            return {
              range: model.getWordUntilPosition(position).range,
              contents: hoverContents
            }
          } else if (word.word === 'sequential' || word.word === 'sequential=sequential' || word.word === 'sequential=True' ||
                  word.word === 'sequential=False' || word.word === 'self.sequential') {
            const description = apiReference.sequential.description
            const hoverContents = [{ value: `**sequential:** ${description}` }]
            return {
              range: model.getWordUntilPosition(position).range,
              contents: hoverContents
            }
          } else if (word.word === 'candles' || word.word === 'self.candles') {
            const description = apiReference.sequential.description
            const hoverContents = [{ value: `**candles:** ${description}` }]
            return {
              range: model.getWordUntilPosition(position).range,
              contents: hoverContents
            }
          }
          // Handle sourceType hover
          if (word.word === 'source_type') {
            // Show all keys in sourceType
            const allKeysDescription = Object.entries(sourceType).map(([key, { description }]) => ({ value: `**${key}:** ${description}` }))
            return {
              range: model.getWordUntilPosition(position).range,
              contents: allKeysDescription
            }
          } else if (sourceType[word.word]) {
            // Show description for a specific key
            const description = sourceType[word.word].description
            return {
              range: model.getWordUntilPosition(position).range,
              contents: [{ value: `**${word.word}:** ${description}` }]
            }
          }

          // Handle devType hover
          if (word.word === 'devtype') {
            // Show all entries in devType
            const allEntriesDescription = Object.entries(devType).map(([key, { description, number }]) => {
              return {
                range: model.getWordUntilPosition(position).range,
                value: `**${key}:** ${description} (Number: ${number})`
              }
            })
            return {
              range: model.getWordUntilPosition(position).range,
              contents: allEntriesDescription
            }
          }
          // Handle maType hover
          if (word.word === 'matype' || word.word === 'ma_matype') {
            // Show all entries in maType
            const allEntriesDescription = Object.entries(matype).map(([key, { description, number }]) => {
              return {
                range: model.getWordUntilPosition(position).range,
                value: `**${key}:** ${description} (Number: ${number})`
              }
            })
            return {
              range: model.getWordUntilPosition(position).range,
              contents: allEntriesDescription
            }
          }
          if (word.word === 'pattern_type') {
            const patternType = indicatorsInfo.pattern_recognition.patternType
            const hoverContents = Object.entries(patternType).map(([key, value]) => {
              return {
                range: model.getWordUntilPosition(position).range,
                value: `**${key}:** ${value}`
              }
            })
            return {
              range: model.getWordUntilPosition(position).range,
              contents: hoverContents
            }
          }
          if (word.word === 'penetration') {
            const penetrationDescription = indicatorsInfo.pattern_recognition.penetration
            const hoverContents = [{ value: `**Penetration:** ${penetrationDescription}` }]
            return {
              range: model.getWordUntilPosition(position).range,
              contents: hoverContents
            }
          }
          if (word.word === 'mode') {
            const pivotMode = indicatorsInfo.pivot.pivot_mode
            const hoverContents = Object.entries(pivotMode).map(([key, { description, number }]) => {
              return {
                range: model.getWordUntilPosition(position).range,
                value: `**${key} (Number: ${number}):** ${description}`
              }
            })

            return {
              range: model.getWordUntilPosition(position).range,
              contents: hoverContents
            }
          }
          // Start from the current line and search upwards
          let lineNumber = position.lineNumber
          let fullLineContent = ''
          let foundIndicator = false

          while (lineNumber > 0) {
            const lineContent = model.getLineContent(lineNumber)
            fullLineContent = lineContent + fullLineContent // Prepend current line

            if (lineContent.includes('ta.')) {
              foundIndicator = true
              break // Stop if the start of the indicator call is found
            }

            lineNumber--
          }

          if (foundIndicator) {
            const indicatorMatch = fullLineContent.match(/ta\.([a-zA-Z0-9_]+)\(/)
            if (indicatorMatch) {
              const indicatorName = indicatorMatch[1]
              if (indicatorsInfo[indicatorName] && indicatorsInfo[indicatorName].parameterDescription) {
                const paramDesc = indicatorsInfo[indicatorName].parameterDescription[word.word]
                if (paramDesc) {
                  return { contents: [{ value: `**${word.word}:** ${paramDesc}` }] }
                }
              }
            }
          }

          let foundUtilsMethod = false
          let methodName = ''
          lineNumber = position.lineNumber
          fullLineContent = ''
          // Search backwards from the current line to find 'utils.'
          while (lineNumber > 0) {
            const lineContent = model.getLineContent(lineNumber)
            fullLineContent = lineContent + fullLineContent // Prepend current line

            if (lineContent.includes('utils.')) {
              foundUtilsMethod = true
              const match = lineContent.match(/utils\.([a-zA-Z0-9_]+)\(/)
              if (match) {
                methodName = match[1]
                break // Found the utils method
              }
            }

            lineNumber--
          }

          if (foundUtilsMethod) {
            const word = model.getWordAtPosition(position)
            const methodInfo = utilsReference[methodName]

            if (methodInfo && methodInfo.properties) {
              const propertyInfo = methodInfo.properties[word.word]
              if (propertyInfo) {
                const returnType = propertyInfo.returnType
                const description = propertyInfo.description
                if (returnType && description) {
                  return {
                    range: model.getWordUntilPosition(position).range,
                    contents: [{ value: `**${word.word} (${returnType}):** ${description}` }]
                  }
                }
              }
            }
          }
        }
      }

      )
    },
